/*
 *  Footer
 *    Scripts for the footer
 *
 *  Version:
 *    1.0.0 - 2021/08/25
 */

import { initAccordion, closeAccordion, openAccordion } from '../objects/accordion/accordion';
import { SMALL } from '../settings/responsive';

// Selectors
const SELECTOR_CONTAINER = '[data-footer]';
const SELECTOR_ACCORDION = '[data-accordion-container]';

const footer = document.querySelector<HTMLElement>(SELECTOR_CONTAINER);
if(footer) {
  const accordionContainer = footer.querySelector<HTMLElement>(SELECTOR_ACCORDION);

  if(accordionContainer) {
    // We always initialise the accordion
    // - the event handler will check sizes before firing
    initAccordion({ container: accordionContainer, disableSize:'SMALL' });

    // But we'll open or close the accordions based on the size
    if(window.innerWidth < SMALL) {
      closeAccordion(accordionContainer);
    } else {
      openAccordion(accordionContainer);
    }

    // Open/close accordion based on window size
    window.addEventListener('resize', () => {
      if(window.innerWidth < SMALL) {
        closeAccordion(accordionContainer);
      } else {
        openAccordion(accordionContainer);
      }
    });
  }
}
